
"use client";

import React, { createContext, useContext, useEffect, useState, ReactNode } from 'react';

type Theme = "light" | "dark";

interface ThemeProviderProps {
  children: ReactNode;
  defaultTheme?: Theme;
  storageKey?: string;
}

interface ThemeProviderState {
  theme: Theme;
  setTheme: (theme: Theme) => void;
  toggleTheme: () => void;
}

const ThemeProviderContext = createContext<ThemeProviderState>({
  theme: "light",
  setTheme: () => {},
  toggleTheme: () => {},
});

export function ThemeProvider({
  children,
  defaultTheme = "light",
  storageKey = "chemlearn-ui-theme",
}: ThemeProviderProps) {
  const [theme, setThemeInternal] = useState<Theme>(defaultTheme);

  useEffect(() => {
    // Load theme from localStorage on initial client-side mount
    const storedTheme = window.localStorage.getItem(storageKey) as Theme | null;
    if (storedTheme) {
      setThemeInternal(storedTheme);
    }
    // Note: System preference detection can be added here if `defaultTheme` is 'system'
    // else if (defaultTheme === 'system' && window.matchMedia("(prefers-color-scheme: dark)").matches) {
    //   setThemeInternal("dark");
    // }
    // else if (defaultTheme === 'system') {
    //   setThemeInternal("light");
    // }
  }, [storageKey, defaultTheme]);

  useEffect(() => {
    // Apply theme to DOM and save to localStorage whenever theme changes
    const root = window.document.documentElement;
    root.classList.remove("light", "dark");
    root.classList.add(theme);
    try {
      window.localStorage.setItem(storageKey, theme);
    } catch (error) {
      console.error("Error saving theme to localStorage", error);
    }
  }, [theme, storageKey]);

  const setTheme = (newTheme: Theme) => {
    setThemeInternal(newTheme);
  };

  const toggleTheme = () => {
    setThemeInternal((prevTheme) => (prevTheme === "light" ? "dark" : "light"));
  };

  return (
    <ThemeProviderContext.Provider value={{ theme, setTheme, toggleTheme }}>
      {children}
    </ThemeProviderContext.Provider>
  );
}

export const useTheme = () => {
  const context = useContext(ThemeProviderContext);
  if (context === undefined) {
    throw new Error("useTheme must be used within a ThemeProvider");
  }
  return context;
};
