
import { doc, getDoc, setDoc } from 'firebase/firestore';
import { db } from '@/lib/firebase';
import type { User } from '@/lib/types';

export async function getUserRole(uid: string): Promise<User['role']> {
  try {
    const userDocRef = doc(db, 'users', uid);
    const userDocSnap = await getDoc(userDocRef);
    if (userDocSnap.exists()) {
      return userDocSnap.data()?.role as User['role'] || 'user';
    }
    return 'user'; // Default to 'user' if document doesn't exist
  } catch (error) {
    console.error("Error fetching user role:", error);
    return 'user'; // Return 'user' as a fallback on error
  }
}

export async function createUserProfile(uid: string, email: string | null, displayName?: string | null): Promise<void> {
    const userDocRef = doc(db, 'users', uid);
    const userProfile: Partial<User> = {
        uid,
        email,
        role: 'user', // Default role
        displayName: displayName || email?.split('@')[0] || 'ChemLearner'
    };
    // Check if user profile already exists (e.g. if using social logins that might pre-create)
    const docSnap = await getDoc(userDocRef);
    if (!docSnap.exists()) {
        await setDoc(userDocRef, userProfile);
    } else {
        // Optionally update if exists, or just ensure role is set
        if (!docSnap.data()?.role) {
             await setDoc(userDocRef, { role: 'user' }, { merge: true });
        }
    }
}
