
"use client";

import type { UserProgress, QuizAnswers } from '@/lib/types';
import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';

interface UserProgressContextType {
  progress: UserProgress;
  completeLesson: (lessonId: string) => void;
  completeQuiz: (quizId: string, score: number, totalQuestions: number) => void;
  getQuizScore: (quizId: string) => { score: number; total: number } | undefined;
  isLessonCompleted: (lessonId: string) => boolean;
  isQuizCompleted: (quizId: string) => boolean;
  setLastAccessedItem: (lectureId: string, type: 'lesson' | 'quiz', id: string) => void;
  getLastAccessedItem: () => { lectureId: string; type: 'lesson' | 'quiz'; id: string } | undefined;
  resetProgress: () => void;
}

const UserProgressContext = createContext<UserProgressContextType | undefined>(undefined);

const defaultProgress: UserProgress = {
  completedLessons: [],
  completedQuizzes: [],
  quizScores: {},
};

const LOCAL_STORAGE_KEY = 'chemlearn_user_progress';

export const UserProgressProvider = ({ children }: { children: ReactNode }) => {
  const [progress, setProgress] = useState<UserProgress>(defaultProgress);
  const [isInitialized, setIsInitialized] = useState(false);

  useEffect(() => {
    const storedProgress = localStorage.getItem(LOCAL_STORAGE_KEY);
    if (storedProgress) {
      try {
        setProgress(JSON.parse(storedProgress));
      } catch (error) {
        console.error("Failed to parse user progress from localStorage", error);
        localStorage.removeItem(LOCAL_STORAGE_KEY); // Clear corrupted data
        setProgress(defaultProgress);
      }
    } else {
      setProgress(defaultProgress);
    }
    setIsInitialized(true);
  }, []);

  useEffect(() => {
    if (isInitialized) {
      localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(progress));
    }
  }, [progress, isInitialized]);

  const completeLesson = (lessonId: string) => {
    setProgress(prev => ({
      ...prev,
      completedLessons: prev.completedLessons.includes(lessonId) ? prev.completedLessons : [...prev.completedLessons, lessonId],
    }));
  };

  const completeQuiz = (quizId: string, score: number, totalQuestions: number) => {
    setProgress(prev => ({
      ...prev,
      completedQuizzes: prev.completedQuizzes.includes(quizId) ? prev.completedQuizzes : [...prev.completedQuizzes, quizId],
      quizScores: {
        ...prev.quizScores,
        [quizId]: { score, total: totalQuestions, timestamp: Date.now() },
      },
    }));
  };

  const getQuizScore = (quizId: string) => {
    return progress.quizScores[quizId];
  };

  const isLessonCompleted = (lessonId: string) => {
    return progress.completedLessons.includes(lessonId);
  };

  const isQuizCompleted = (quizId: string) => {
    return progress.completedQuizzes.includes(quizId);
  };
  
  const setLastAccessedItem = (lectureId: string, type: 'lesson' | 'quiz', id: string) => {
    setProgress(prev => ({ ...prev, lastAccessedItemId: { lectureId, type, id } }));
  };

  const getLastAccessedItem = () => {
    return progress.lastAccessedItemId;
  };

  const resetProgress = () => {
    setProgress(defaultProgress);
    localStorage.removeItem(LOCAL_STORAGE_KEY);
  }

  if (!isInitialized) {
    return null; // Or a loading spinner
  }
  
  return (
    <UserProgressContext.Provider value={{ progress, completeLesson, completeQuiz, getQuizScore, isLessonCompleted, isQuizCompleted, setLastAccessedItem, getLastAccessedItem, resetProgress }}>
      {children}
    </UserProgressContext.Provider>
  );
};

export const useUserProgress = () => {
  const context = useContext(UserProgressContext);
  if (context === undefined) {
    throw new Error('useUserProgress must be used within a UserProgressProvider');
  }
  return context;
};
